	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"/static/Kontribute.jpg\"></p>\n<p>Kontribute is a mobile event planning app that is supported on all mobile platforms.\nThe host is able to create an event page that contains information such as location, time, attendees, and a list of required party items.\nOn the other hand, attendees are able to choose the items that they can bring to the party and share the event on social media (Facebook and WhatsApp).\nEvents can be easily organized by using template lists that contain items that are typically needed for a type of event (e.g. football night, house party)\nMoreover, the list system divides up responsibilities and ensures just the right amount of supplies for the event.</p>\n<p><strong>Link:</strong><br>\nGithub repository: <a href=\"https://github.com/christophercypang/kontributerino\">Kontribute</a>.</p>\n<p><strong>Team:</strong><br>\nThe team was made up of 4 people (4 developers).</p>\n<p><strong>Work:</strong><br>\nThe app was implemented using AngularJS, Ionic, and Parse (originally used Firebase).\nI implemented the list system, various UI logic, and features such as default templates and creating custom templates.</p>\n<p><img src=\"/static/KontributeList.jpg\"></p>\n<p><img src=\"/static/KontributeEvent.jpg\"></p>","frontmatter":{"date":"November 28, 2015","path":"/Kontribute","title":"Kontribute","image":"/static/Kontribute.jpg"}}},"pathContext":{}}

